.article-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  align-self: stretch;
}
.article-title {
  flex: 1 0 0;

  color: #252525;

  /* Heading 2 */
  font-family: Outfit;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 44.8px; /* 140% */
  letter-spacing: -0.5px;
}

.article-brief {
  color: var(--Text-Paragraph, #333);

  /* Paragraph */
  font-family: Roboto Mono;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 175% */
}
.article-content {
  color: var(--Text-Paragraph, #333);

  /* Paragraph */
  font-family: Roboto Mono;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 175% */
}
