.layout-container {
  display: flex;
  width: auto;
  padding: 0px 30%;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
  background: var(--Background, #fafaf8);
}

.wrapper {
  display: flex;
  width: auto;
  padding: 64px 32px 32px 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
}

.see-all {
  color: #333;

  /* Link */
  font-family: Outfit;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 175% */
}

@media only screen and (min-width: 384px) and (max-width: 767px) {
  .layout-container {
    padding: 0px 0px;
  }
}
