.about-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  align-self: stretch;
}

.about-title {
  align-self: stretch;
  color: var(--Text-Headings, #252525);

  /* Heading 1 */
  font-family: Outfit;
  font-size: 36px;
  font-style: normal;
  font-weight: 700;
  line-height: 43.2px; /* 120% */
  letter-spacing: -1.5px;
}

.about-content {
  align-self: stretch;
  color: var(--Text-Paragraph, #333);

  /* Paragraph */
  font-family: Roboto Mono;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 175% */
}

.about-contact {
  color: var(--Text-Paragraph, #333);

  /* Paragraph */
  font-family: Roboto Mono;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 175% */
}
