.thoughts-wrapper {
  display: flex;
  padding-bottom: 16px;
  flex-direction: column;
  align-items: flex-start;
  align-self: stretch;
}

.thoughts-header {
  display: flex;
  padding: 16px 0px;
  align-items: center;
  gap: 32px;
  align-self: stretch;
}

.thoughts-title {
  flex: 1 0 0;

  color: #252525;

  /* Heading 2 */
  font-family: Outfit;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 44.8px; /* 140% */
  letter-spacing: -0.5px;
}
