.contact-section-wrapper {
  display: flex;
  padding: 16px 0px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  align-self: stretch;
}

.contact-text {
  color: #000;

  /* Paragraph */
  font-family: Roboto Mono;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 175% */
}
.social-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}
.links-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
}
