.card-wrapper {
  display: flex;
  padding: 8px 0px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: stretch;
}

.card-container {
  display: flex;
  padding: 16px;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;
  align-self: stretch;
  border-radius: 16px;
  background: var(--Card-Background, #f5f5f5);
}
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: stretch;
}
.card-content {
  display: flex;
  align-items: center;
  gap: 10px;
  align-self: stretch;
}

.card-title {
  color: var(--Text-Headings, #252525);

  /* Heading 3 */
  font-family: Outfit;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 33.6px; /* 140% */
}

.card-title-tag-background {
  display: flex;
  padding: 2.797px 7.406px 2.797px 8px;
  align-items: flex-start;
  border-radius: 8px;
  background: #fafaf8;
}

.card-title-tag {
  color: #333;

  /* Paragraph */
  font-family: Roboto Mono;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 175% */
}
.card-text {
  flex: 1 0 0;

  color: var(--Text-Paragraph, #333);

  /* Paragraph */
  font-family: Roboto Mono;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 175% */
}
