.thoughts-card-wrapper {
  display: flex;
  padding: 16px 0px;
  align-items: flex-start;
  gap: 12px;
  align-self: stretch;

  border-bottom: 1px solid #333;
  background: rgba(245, 245, 245, 0);
}
.thoughts-card-title-wrapper {
  display: flex;
  align-items: center;
  gap: 10px;
  flex: 1 0 0;
}
.thoughts-card-title-text {
  flex: 1 0 0;

  color: var(--Text-Paragraph, #333);

  /* Paragraph */
  font-family: Roboto Mono;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 175% */
}
