.footer-wrapper {
  display: flex;
  padding: 16px 0px;
  flex-direction: column;
  align-items: flex-start;
  align-self: stretch;
}
.footer-container {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;

  border-top: 1px solid #4b4b4b;
}
.footer-content {
  display: flex;
  padding: 16px 0px;
  justify-content: space-between;
  align-items: flex-start;
  align-self: stretch;
}
.footer-text {
  color: #4b4b4b;

  /* Paragraph (footer) */
  font-family: Roboto Mono;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px; /* 233.333% */
}
